body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.input-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 10%;
    width: 25%;
    margin: 10px;

}

.label {
    color: black;
    font-size: 14px;
    margin: 5px;
    margin-right: 15px;    
}

.measure {
    width: 30%
}
.answer {
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 0px;
    margin: auto;
    font-size: 64px;
    color: black;
}
.slider {
    position: relative;
    margin: 32px auto;
    width: 350px;
    display: block;
    background: #565656;
    height: 7px;
    box-shadow: 0 1px 0px rgba(255,255,255,0.25),
      inset 0 0 4px rgba(0,0,0,0.9);

}
.single-atom {
    height: 60px;
    width: 60px;
    margin: 8px;
}
.atoms-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.App {
  text-align: center;
  
}


.backer {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.mainbox {
  position: relative;
  height: 400px;
  width: 50%;
  background-color: white;
  outline-style: dotted;
  outline-color: blue;
  padding: 10px;
}

.decay-slider-pair {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.answer-component{
  position: absolute;
  bottom: 0px;
}



